<template>
  <div class="content">
    <header class="header">
      <div class="header-container">
        <h3><span style="cursor: pointer" @click="toIndex">高管负面监测</span></h3>
        <div class="input-box">
          <input type="text" id="searchInput" v-model="mySearchVal" @keyup="keyup($event)">
          <i class="icon iconfont icon-chaxun" id="searchBtn" title="搜索"  @click="getSearch"></i>
        </div>
      </div>
    </header>
    <div class="warn-box" v-if="warnShow">
      <div class="warn-box-content">
        <p>
          <img src="../assets/img/warn-icon_03.png" alt="">
          <span style="width: 74px; text-align: right">异常提醒：</span><span>【裁判文书】深圳市腾讯计算机系统有限公司与马化腾侵害作品信息网络传播权纠纷二审民事裁定书</span>
          <i class="icon iconfont icon-guanbi fr" @click="warnClick"></i>
        </p>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>
<script>
  export default{
    name: 'layout',
    data(){
      return {
        warnShow:false,
        mySearchVal: ''
      }
    },
    mounted(){
    },
    methods: {
      toIndex() {
        this.$router.push({path:'/'})
      },
      warnClick() {
        this.warnShow = false
      },
      keyup(ev) {
        if (ev.keyCode == 13) {
          this.$router.push({path:'/layout', query:{'searchVal':this.mySearchVal}})
        }
      },
      getSearch() {
         this.$router.push({path:'/layout', query:{'searchVal':this.mySearchVal}})
      }
    }
  }
</script>
<style scoped lang="scss">
  .warn-box{
    height: 31px;
    position: fixed;
    width: 100%;
    background: #f3e4cd;
    border-bottom: 1px solid #feb752;
    z-index: 999999;
    top: 0;
  }
  .warn-box-content{
    width: 1200px;
    height: 31px;
    margin: auto;
    line-height: 30px;
    color: #ea8a00;
    font-size: 14px;
  }
  .warn-box-content p img{
    width: 16px;
    height: 16px;
    border-radius: 50%;
    vertical-align: middle;
  }
  .warn-box-content p span{
    display: inline-block;
    vertical-align: middle;
  }
  .warn-box-content p i{
    vertical-align: middle;
    cursor: pointer;
  }
  .warn-box-content p i:hover{
    opacity: .6;
  }
  .header{
   height: 60px;
   background: #2a94e5;
 }
 .header-container{
   width: 1200px;
   margin: auto;
 }
 .header-container h3{
   display: inline-block;
   float: left;
   font-size: 20px;
   color: #ffffff;
   line-height: 60px;
 }
 .header-container .input-box{
   display: inline-block;
   float: right;
   width: 442px;
   height: 30px;
   position: relative;
   padding-top: 14px;
 }
 .header-container .input-box input{
   width: 100%;
   height: 30px;
   padding: 0 40px 0 10px;
   box-sizing: border-box;
   border-radius: 3px;
   background: #dcedf9;
   border: 1px solid #dcedf9;
 }
 input:focus{
   box-shadow: 0 0 5px #b3ddfd;
 }
 .header-container .input-box i{
   position: absolute;
   right: 15px;
   top: 20px;
   font-size: 18px;
   color: #117ed6;
   cursor: pointer;

 }
 .header-container .input-box i:hover{
   opacity: .7;
 }
</style>



