<template>
  <div class="content">
    <div class="content-title">搜索关键词：<span style="color: #16a7e3; margin-right: 10px" v-text="searchName"></span>   共找到 <span style="color: #ea8416; margin: 0 5px"><span v-text="total"></span>条</span>搜索结果</div>
    <div class="content-left fl">
      <dl>
        <dd v-for="personItem in searchList" @click="personItemClick(personItem)">
          <div class="person-left fl">
            <img :src="personItem.purl" alt="" @error="imgError(personItem)">
          </div>
          <div class="person-right fr">
            <h3><span v-text="personItem.pname"></span></h3>
            <p v-for="positionItem in personItem.positionList" v-text="positionItem"></p>
          </div>
        </dd>
      </dl>
      <div class="pagination-wrap cf">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
        <el-button slot class="pagenation-btn">确定</el-button>
      </div>
    </div>
    <div class="content-right fr">
      <dl>
        <dt>您可能感兴趣的人</dt>
        <dd v-for="personItem in lovePersonList" @click="personItemClick(personItem)">
        <div class="person-left fl">
          <img :src="personItem.purl" alt="" @error="imgError(personItem)">
        </div>
        <div class="person-right fr">
          <h3><span v-text="personItem.pname"></span></h3>
          <p v-for="positionItem in personItem.positionList" v-text="positionItem"></p>
        </div>
      </dd>
      </dl>
    </div>
  </div>
</template>
<script>
  import {NoData, Loading} from '../components'
  export default{
    name: 'searchList',
    data(){
      return {
        total:0,
        searchName:'',
        searchVal:'',
        lovePersonList:[],
        searchList:[],
        currentPage: 1,
        pageSize: 8
      }
    },
    mounted(){
      this.initPage()
      let _this = this;
      document
        .querySelector("#searchBtn")
        .addEventListener("click", function() {
          let serachVal = document.querySelector("#searchInput").value;
          _this.serachVal = serachVal;
          _this.getSearchList();
        });
      document
        .querySelector("#searchInput")
        .addEventListener("keyup", function(ev) {
          if (ev.keyCode == 13) {
            let serachVal = document.querySelector("#searchInput").value;
            _this.serachVal = serachVal;
            _this.getSearchList();
          }
        });
    },
    methods: {
      initPage() {
        this.searchVal = this.$route.query.searchVal
        this.loveList()
        this.getSearchList()
      },
      personItemClick(item) {
        this.$router.push({path:'/layout/detail', query:{personId:item.personId}})
      },
      imgError(personItem) {
        personItem.purl = '../../static/img/noperson_03.png'
      },
      loveList() {
        this.api.Get('loveList',{}).then(res=>{
          this.lovePersonList = res.data
      }).catch(res=>{
          console.log(res)
      })
      },
      getSearchList() {
        let _this = this
        this.api.Post('searchList',this.qs({
          searchVal:_this.searchVal,
          pageSize: _this.pageSize,
          page:_this.currentPage
        })).then(res=>{
          this.total = res.total
          this.searchName = res.data[0].pname
          this.searchList = res.data
      }).catch(res=>{
          console.log(res)
      })
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getSearchList();
      }
    }
  }
</script>
<style scoped lang="scss">
  .pagenation-btn{
    float: right;
    box-sizing: border-box;
    padding: 7px 12px;
    margin-right: 12px;
  }
  .content{
    width: 1200px;
    margin: auto;
  }
  .content-title{
    font-size: 14px;
    color: #333333;
    line-height: 48px;
    padding-top: 14px;
  }
  .content-left{
    width: 898px;
  }
  .content-right{
    width: 302px;
    border: 1px solid #dbdbdb;
    padding: 0 16px;
    box-sizing: border-box;
    background: #ffffff;
  }
  .content-right dt{
    height: 14px;
    border-left: 2px solid #3da9f7;
    line-height: 16px;
    font-size: 16px;
    margin: 17px 0 10px 0;
    padding-left: 10px;
    color: #333333;
  }
  .content-right dd{
    min-height: 125px;
    border-bottom: 1px dashed #dbdbdb;
    padding: 20px 0 14px 0;
    box-sizing: border-box;
    cursor: pointer;
  }
  .content-right dd:last-child{
    border-bottom:none
  }
  .content-right .person-left{
   width: 60px;
  }
  .content-right .person-left img{
    width: 60px;
    height: 60px;
    border-radius: 50%;
  }
  .content-right .person-right{
   width: 200px;
  }
  .content-right .person-right h3 span{
    line-height: 28px;
    color: #333333;
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;
  }
  .content-right .person-right h3 span:hover{
    text-decoration: underline;
    color: #35aee5;
  }
  .content-right .person-right p{
    line-height: 20px;
    font-size: 14px;
    color: #666666;
  }

  .content-left dd{
    width: 437px;
    border: 1px solid #dbdbdb;
    padding: 20px;
    box-sizing: border-box;
    height: 197px;
    float: left;
    margin: 0 12px 12px 0;
    background: #ffffff;
    cursor: pointer;
  }
  .content-left dd:hover{
    box-shadow: 0 0 12px #e2e6e8;
  }
  .content-left .person-left{
    width: 80px;
  }
  .content-left .person-left img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
  }
  .content-left .person-right{
    width: 290px;
  }
  .content-left .person-right h3 span{
    line-height: 50px;
    color: #333333;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
  }
  .content-left .person-right h3 span:hover{
    color: #35aee5;
  }
  .content-left .person-right p{
    line-height: 30px;
    font-size: 14px;
    color: #666666;
  }
</style>



