import echarts from 'echarts'

export default function causeBar(id, data) {
  let yearArr = []
  let valueArr = []
  data.forEach(function(item) {
    yearArr.push(item.year)
    valueArr.push(item.count)
  })
  let myChart = echarts.init(document.getElementById(id))
  let option = {
      grid:{
        left:30,
        bottom:30,
        right:30,
        top:40,
      },
      color: [ new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
        offset: 0,
        color: '#3da9f7'
      }, {
        offset: 1,
        color: '#1f77d1'
      }])],
    tooltip : {
      trigger: 'axis'
    },
    calculable : true,
    xAxis : [
      {
        type : 'category',
        axisTick:{
          interval: 0
        },
        axisLine: {
          lineStyle: {
            color:'#ffffff'
          }
        },
        axisLabel:{
          interval: 0,
          textStyle:{
            fontSize:12,
            color:'#333333'
          }
        },
        data : yearArr
      }
    ],
    yAxis : [
      {
        type : 'value',
        splitLine: {
          show: false,
          lineStyle: {
            color: '#ccc',
            type : "solid"
          }
        },
        axisLine: {
          lineStyle: {
            color:'#ffffff'
          }
        }
      }
    ],
    series : [
      {
        name:'涉诉数量',
        type:'bar',
        barWidth: 18,
        data:valueArr,
        markPoint : {
          symbolSize: 40,
          itemStyle:{
            normal:{
              color:'#4dc1f9'
            }
          },
          data : [
            {type : 'max', name: '最大值'},
            {type : 'min', name: '最小值'}
          ]
        }
      }
    ]
  };


  myChart.setOption(option)
}





