<template>
  <div class="content">
    <div class="content-top">
      <div class="content-container">
        <h2>高管负面查询</h2>
        <div class="input-box">
          <input type="text" @keyup="keyup($event)" placeholder="请输入人名、人名加地域/公司名称关键词，如“马云 阿里”、“刘强东 京东”">
          <span class="search-btn" @click="getSearch">
            <i class="icon iconfont icon-chaxun"></i>
          </span>
        </div>
        <div class="hot-box clearfix">
          <div class="hot-box-left fl">热搜：</div>
          <div class="hot-box-right fl">
            <ul>
              <li v-for="entItem in hotData" @click="hotClick">
                <a v-text="entItem.legalperson"></a>
                <span v-text="entItem.entName"></span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <div class="content-bottom">
      <div class="content-container">
        <ul>
          <li>
            <img src="../assets/img/index_03.png" alt="">
            <p>查看涉诉信息</p>
          </li>
          <li>
            <img src="../assets/img/index_05.png" alt="">
            <p>监测关联企业风险问题</p>
          </li>
          <li>
            <img src="../assets/img/index_07.png" alt="">
            <p>及时发现负面舆情</p>
          </li>
          <li>
            <img src="../assets/img/index_09.png" alt="">
            <p>挖掘关联任务风险信息</p>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'index',
    data(){

      return {
        hotData:[]
      }
    },
    mounted(){
      this.initPage()
    },
    activated() {
    },
    methods: {
      initPage() {
        this.api.Post('getHotList').then(res=>{

          this.hotData = res.data
        }).catch(res=>{
          console.log(res)
        })
      },
      keyup(ev) {
        if (ev.keyCode == 13) {
          this.$router.push('/layout')
        }
      },
      getSearch() {
        this.$router.push('/layout')
      },
      hotClick() {
        this.$router.push('/layout/detail')
      }
    }
  }
</script>
<style scoped lang="scss">
  .content-top{
    height: 580px;
    background: url("../assets/img/idex-bg_03.png") no-repeat center;
    background-size: cover;
  }
  .content-container{
    width: 1200px;
    margin: auto;
  }
  .content-top .content-container h2{
    line-height: 206px;
    font-size: 40px;
    color: #ffffff;
    padding-top: 93px;
    text-align: center;
    font-weight: 700;
  }
  .content-top .content-container .input-box{
    width: 830px;
    height: 100px;
    margin: auto;
    position: relative;
  }
  .content-top .content-container .input-box input{
    width: 700px;
    height: 48px;
    background: #d3dfed;
    border: 1px solid #d3ddec;
    box-sizing: border-box;
    font-size: 14px;
    padding: 0 10px;
  }
  .content-top .content-container .search-btn{
    display: block;
    position: absolute;
    width: 130px;
    height: 48px;
    right: 0;
    top: 0;
    background: -webkit-linear-gradient(left, #39a2ee, #0876d1);
    background: -moz-linear-gradient(left, #39a2ee, #0876d1);
    background: -o-linear-gradient(left, #39a2ee, #0876d1);
    background: linear-gradient(left, #39a2ee, #0876d1);
    cursor: pointer;
  }
  .content-top .content-container .search-btn:hover{
    opacity: .9;
  }
  .content-top .content-container .search-btn i{
    display: inline-block;
    font-size: 28px;
    color: #ffffff;
    margin-left: 50px;
    margin-top: 10px;
  }
  input:focus{
    box-shadow: 0 0 5px #b3ddfd;
  }
 .content-top .hot-box{
    width: 830px;
    margin: auto;
  }
 .hot-box-left{
   width: 104px;
   line-height: 40px;
   font-size: 16px;
   color: #ffffff;
   text-align: right;
 }
  .hot-box-right{
    width: 726px;
  }
  .hot-box-right li{
    display: inline-block;
    line-height: 40px;
    height: 40px;
    padding: 0 20px;
    min-width: 140px;
    background: #1f89d8;
    box-sizing: border-box;
    opacity: .8;
    border-radius: 5px;
    font-size: 16px;
    color: #ffffff;
    margin: 0 10px;
    cursor: pointer;
  }
  .hot-box-right li:hover{
    opacity: 1;
  }
  .hot-box-right li a{
    cursor: pointer;
    color: #ffd86f;
    margin-right: 8px;
  }
  .hot-box-right li span{
    cursor: pointer;
    color: #ffffff;
    cursor: pointer;
  }
  .hot-box-right li span:hover{
    text-decoration: underline;
  }

  .content-bottom{
    height: 400px;
    padding-top: 30px;
    box-sizing: border-box;
  }
  .content-bottom li{
    float: left;
    width: 25%;
    border-right: 1px solid #f4f4f4;
    box-sizing: border-box;
  }
  .content-bottom li img{
    width: 100%;
  }
  .content-bottom li p{
    text-align: center;
    line-height: 60px;
    color: #333333;
    font-size: 20px;
  }
</style>



