<template>
  <div class="co-content">
    <div class="top-container">
      <div class="co-content-left fl">
        <div class="num-item">
          <div class="num-item-left fl">舆情总量</div>
          <div class="num-item-right fl"><span v-text="sentimentNumInfo.totalNum"></span><label>（条）</label></div>
        </div>
        <div class="num-item">
          <div class="num-item-left fl">负面舆情</div>
          <div class="num-item-right fl"><span v-text="sentimentNumInfo.negativeNum"></span><label>（条）</label></div>
        </div>
        <div class="des-item">
          <div class="des-item-left fl">今日新增信息总量：<span v-text="sentimentNumInfo.todayNum"></span><label>条</label></div>
          <div class="des-item-right fl">今日新增负面舆情：<span v-text="sentimentNumInfo.todayNegative"></span><label>条</label></div>
        </div>
      </div>
      <div class="co-content-right fr">
        <h3 class="module-title" style="margin-top: 20px;margin-bottom: 15px">负面舆情走势</h3>
        <div class="chart-box-container" id="chart"></div>
      </div>
    </div>
    <div class="bottom-container">
      <div class="bottom-content cf">
        <div class="list-title">
          <h3 class="module-title fl" style="margin-top: 12px">近期负面舆情</h3>
          <div class="order-box fr">
            <label>排序方式：</label>
            <span :class="{'active':currentIdx == '0'}" @click="tabClick(0)">转载量<i class="icon iconfont icon-xiajiang"></i></span>
            <span :class="{'active':currentIdx == '1'}" @click="tabClick(1)">相关度<i class="icon iconfont icon-xiajiang icontop"></i></span>
            <span :class="{'active':currentIdx == '2'}" @click="tabClick(2)">发布时间<i class="icon iconfont icon-xiajiang icontop"></i></span>
          </div>
        </div>
        <noData v-if="noData"></noData>
        <loading :Lshow.sync="Lshow"></loading>
        <div class="list-content cf" v-if="!Lshow">
          <div class="list-item" v-for="(item, idx) in sentimentList" @click="listClick(item)">
            <span class="indexbox">
              {{idx+1}}
              <label class="sanjiao"></label>
            </span>
            <h3 v-text="item.title"></h3>
            <div class="info-box">
              <span style="width: 150px" v-text="item.date"></span>
              <label>来源：</label>
              <span style="width: 92px" v-text="item.resource"></span>
              <label style="color: #ed9f4b">转载量：</label>
              <span style="color: #ed9f4b" v-text="item.reloadNum"></span>
            </div>
            <p v-text="item.content"></p>
          </div>
        </div>
      </div>
      <div  v-if="!Lshow" class="pagination-wrap cf">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
        <el-button slot class="pagenation-btn">确定</el-button>
      </div>
    </div>
  </div>
</template>
<script>
  import noData from './noData.vue'
  import loading from './loading.vue'
  import {sentimentLine} from '../assets/js/echartsComponents'
  export default{
    name: 'publicSentiment',
    data(){
      return {
        noData: false,
        Lshow: true,
        personId:'',
        currentPage: 1,
        pageSize: 8,
        total: 0,
        orderType:0,
        sentimentNumInfo:{},
        currentIdx:0,
        sentimentList:[]
      }
    },
    mounted(){
      this.initPage()
    },
    methods: {
      listClick(item) {
        this.$router.push({path:'/layout/detail/publicSentimentDetail', query:{sentimentId:item.sentimentId}})
      },
      tabClick(idx) {
        this.currentIdx = idx
        this.orderType = idx
        this.getSentimentList();
      },
      initPage() {
        this.personId = this.$route.query.personId
        this.getSentimentLine()
        this.getSentimentNum()
        this.getSentimentList()
      },
      getSentimentLine() {
        this.api.Post('publicSentimentLine',this.qs({personId:this.personId})).then(res=>{
          sentimentLine('chart', res.data)
      }).catch(res=>{
          console.log(res)
      })
      },
      getSentimentList() {
        this.Lshow = true
        this.api.Post('publicSentimentList',this.qs({
          personId:this.personId,
          pageSize: this.pageSize,
          page:this.currentPage,
          orderType:this.orderType
        })).then(res=>{
          this.total = res.total
          this.sentimentList = res.data
          this.Lshow = false
      }).catch(res=>{
          console.log(res)
      })
      },
      getSentimentNum() {
        this.api.Post('sentimentNum',this.qs({personId:this.personId})).then(res=>{
          this.sentimentNumInfo = res.data
      }).catch(res=>{
          console.log(res)
      })
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getSentimentList();
      }
    },
    components:{
      noData,
      loading
    }
  }
</script>
<style scoped lang="scss">
  .pagenation-btn{
    float: right;
    box-sizing: border-box;
    padding: 7px 12px;
    margin-right: 22px;
  }
  .module-title{
    height: 14px;
    width: 200px;
    border-left: 2px solid #3da9f7;
    font-size: 16px;
    line-height: 16px;
    margin-left: 20px;
    padding-left: 10px;
    color: #333333;
  }
  .co-content{
    overflow: hidden;
  }
  .top-container{
    overflow: hidden;
  }
  .co-content-left{
    width: 435px;
    height: 318px;
    border: 1px solid #dbdbdb;
    box-sizing: border-box;
    padding: 60px 30px 0 30px;
  }
  .num-item{
    height: 39px;
    margin-bottom: 30px;
    border: 1px solid #4dc1f9;
  }
  .num-item:nth-child(2){
    border: 1px solid #f1a731;
    margin-bottom: 54px;
  }
  .num-item-right{
    font-weight: 700;
  }
  .num-item:nth-child(1) .num-item-left{
       background: url("../assets/img/sentim-icon_03.png") no-repeat;
       color: #4dc1f9;
       font-size: 14px;
       line-height: 39px;
       text-align: center;
     }

  .num-item:nth-child(1) .num-item-right{
    background: #4dc1f9;
    color: #ffffff;
    font-size: 20px;
    line-height: 39px;
    text-align: left;
  }

  .num-item:nth-child(2) .num-item-left{
    background: url("../assets/img/sentim-icon_06.png") no-repeat;
    color: #f1a731;
    font-size: 14px;
    line-height: 39px;
    text-align: center;
  }

  .num-item:nth-child(2) .num-item-right{
    background: #f1a731;
    color: #ffffff;
    font-size: 20px;
    line-height: 39px;
    text-align: left;
  }
  .num-item-left{
    width: 156px;
    height:39px;
  }
  .num-item-right{
    width: 215px;
    height:39px;
    padding-left: 36px;
    box-sizing: border-box;
  }
  .num-item-right label{
    font-size: 14px;
  }
  .des-item{
    border-top: 2px solid #dbdbdb;
    line-height: 86px;
    font-size: 14px;
    color: #666666;
  }
  .des-item-left{
    width: 50%;
    line-height: 86px;
    text-align: left;
  }
  .des-item-left span{
    color: #6fbdf5;
    font-size: 18px;
    font-weight: 700;
  }
  .des-item-left label{
    color: #6fbdf5;
    font-size: 14px;
  }
  .des-item-right{
    width: 50%;
    line-height: 86px;
    text-align: right;
  }

  .des-item-right span{
    color: #ed9f4b;
    font-size: 18px;
    font-weight: 700;
  }
  .des-item-right label{
    color: #ed9f4b;
    font-size: 14px;
  }
  .co-content-right{
    width: 711px;
    height: 318px;
    border: 1px solid #dbdbdb;
    box-sizing: border-box;
  }
  .chart-box-container{
    height: 266px;
  }
  .bottom-container{
    margin-top: 12px;
    border: 1px solid #dbdbdb;
  }
  .list-title{
    background: #fbfbfb;
    border-bottom: 1px solid #dbdbdb;
    height: 38px;
    line-height: 38px;
  }
  .order-box{
    width: 385px;
    height: 38px;
    font-size: 14px;
    color: #666666;
  }
  .order-box label{
    display: inline-block;
    width: 76px;
    height: 38px;
  }
  .order-box span{
    display: inline-block;
    padding: 0 10px;
    height: 28px;
    line-height: 26px;
    border: 1px solid #dbdbdb;
    margin: 0 3px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .order-box span:hover{
    border: 1px solid #2c9aea;
    color: #ffffff;
    background: #2c9aea;
  }
  .order-box span.active{
    border: 1px solid #2c9aea;
    color: #ffffff;
    background: #2c9aea;
  }
  .order-box span i{
    font-size: 12px;
    display: inline-block;
    margin-left: 5px;
    vertical-align: top;
  }
  .order-box span .icontop{
    transform: rotate(180deg);
    vertical-align: bottom;
  }
  .list-content{
    padding: 0 20px;
  }
  .list-item{
    padding-left: 28px;
    position: relative;
    height: 149px;
    border-bottom: 1px dashed #dbdbdb;
    padding-top: 26px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .list-item:nth-last-child(1){
    border-bottom: 1px solid #dbdbdb;
  }
  .list-item .indexbox{
    display: inline-block;
    width: 17px;
    height: 18px;
    background: #adb4c2;
    font-size: 10px;
    color: #ffffff;
    text-align: center;
    line-height: 18px;
    position: absolute;
    left: 0;
    top: 26px;
    border-radius: 3px;
  }
  .list-item .indexbox .sanjiao{
    width: 4px;
    height: 6px;
    display: inline-block;
    background: url("../assets/img/order-icon_03.png");
    position: absolute;
    right: -4px;
    top: 6px;
  }
  .list-item h3{
    line-height: 18px;
    font-size: 18px;
    color: #333333;
    font-weight: 400;
    cursor: pointer;
  }
  .list-item h3:hover{
    color: #44a2eb;
    text-decoration: underline;
  }
  .list-item .info-box{
    color: #999999;
    font-size: 14px;
    line-height: 40px;
  }
  .list-item .info-box span{
    display: inline-block;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    vertical-align: middle;
  }
  .list-item .info-box label{
    display: inline-block;
    vertical-align: middle;
  }
  .list-item p{
    line-height: 20px;
    color: #747474;
    font-size: 14px;
    height: 40px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
  }
</style>




