import axios from 'axios'
import '../../../static/js/config'
// axios 配置
axios.defaults.timeout = 5000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
 const baseurl = newBaseUrl
const baseurlMock = newBaseurlMock// axios.defaults.withCredentials = true;
let instance = axios.create({
   // headers: {'tokenid': sessionStorage.getItem('tokenid')}
});

let instanceMock = axios.create({

});

export function Post(url, params) {
    return new Promise((resolve, reject) => {
        instance.post(baseurl + url, params)
            .then(response => {
                if (response.data.code == '1') {
                    resolve(response.data);
                }
                else if(response.data.code=='501') {
                    instanceMock.post(baseurlMock + url, params)
                        .then(response => {
                            resolve(response.data);
                        })
                        .catch((error) => {
                            reject(error)
                        })
                }
            })
            .catch((error) => {
                instanceMock.post(baseurlMock + url, params)
                    .then(response => {
                        resolve(response.data);
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
    })
}
export function Get(url, params) {
    return new Promise((resolve, reject) => {
        instance.get(baseurl + url, {params:params})
            .then(response => {
                if (response.data.code == '1') {
                    // console.log('服务器正常返回数据')
                    resolve(response.data);
                } else if(response.data.code=='501') {
                    // console.log('服务器501 ，模拟数据')
                    instanceMock.get(baseurlMock + url, {params:params})
                        .then(response => {
                            resolve(response.data);
                        })
                        .catch((error) => {
                            reject(error)
                        })
                }
            })
            .catch((error) => {
                // console.log('服务器错误！！ 模拟数据')
                instanceMock.get(baseurlMock + url, {params:params})
                    .then(response => {
                        resolve(response.data);
                    })
                    .catch((error) => {
                        reject(error)
                    })
            })
    })
}




