import echarts from 'echarts'

export default function causeBar(id, data) {
  let nameArr = []
  let valueArr = []
  data.forEach(function(item) {
    nameArr.push(item.name)
    valueArr.push(item.value)
  })
  let myChart = echarts.init(document.getElementById(id))
  let option = {
    grid:{
      left:30,
      bottom:25,
      right:57,
      top:0,
    },
    yAxis: [
      {
        inverse: true,
        data:  nameArr
        ,
        axisLabel: {
          textStyle: {
            color: '#8db0ff',
            fontSize:12,
            align:'left',
          },
          show:false
        },
        offset:30,
        splitLine: {
          show: false
        },
        axisTick:{
          show:false
        },
        axisLine: {
          show: false
        }
      },
      {
        inverse: true,
        data: valueArr,
        axisLabel: {
          textStyle: {
            color: '#333333',
            fontSize:12,
            align:'left',
          }
        },
        offset:10,
        splitLine: {
          show: false
        },
        axisTick:{
          show:false
        },
        axisLine: {
          show: false
        }
      }],

    xAxis: {
      max:20,
      show:false,
      axisLine: {
        show: true
      },

    },
    color:['#65b0f5'],
    itemStyle:{
      normal:{
        color: '#4db2e7'
      }
    },
    series: [{
      // 辅助系列
      type: 'bar',
      barGap:'-100%',
      silent: true,
      label: {
        normal: {
          show: true,
          position: [1000,-20],
          formatter:'{c}',
          color:'#666666'
        }
      },
      itemStyle: {
        normal: {
          barBorderRadius: 0,
          color: '#f0f0f0',
        }
      },
      barWidth: 20,
      data: [20,20,20,20,20,20]
    }, {
      type: 'bar',
      data: valueArr,
      barWidth: 20,
      label: {
        normal: {
          show: true,
          position: [1,26],
          formatter:'{b}',
          color:'#666666'
        }
      },
      itemStyle: {
        normal: {
          barBorderRadius:0,
          shadowColor: 'rgba(0, 0, 0, .4)',
          shadowBlur: 0
        }
      }
    }]
  };
  myChart.setOption(option)
}





