<template>
  <div class="co-content">
    <div class="title-box">
      <h3 class="module-title fl">舆情详情</h3>
      <span class="fr" @click="goBack" style="margin-right: 15px"><i class="icon iconfont icon-fanhui"></i><b>返回列表</b></span>
    </div>
    <div class="content-container">
      <noData v-if="noData"></noData>
      <loading :Lshow.sync="Lshow"></loading>
      <div class="content-title" v-if="!Lshow">
        <h4 v-text="publicSentimentDetailInfo.title"></h4>
        <p><label>来源：</label><span style="width: 64px;color: #1dadff"  v-text="publicSentimentDetailInfo.resource"></span><label>转载量：</label><span style="width: 64px;color: #ff6e16"  v-text="publicSentimentDetailInfo.reloadNum"></span><span  v-text="publicSentimentDetailInfo.date"></span></p>
      </div>
      <div class="content-content" v-if="!Lshow">
        <img src="../assets/img/example22_03.png" alt="">
      </div>
    </div>
  </div>
</template>
<script>
  import noData from './noData.vue'
  import loading from './loading.vue'
  export default{
    name: 'publicSentimentDetail',
    data(){
      return {
        noData: false,
        Lshow: true,
        myDialogShow: false,
        medalShow:false,
        publicSentimentDetailInfo:{}
      }
    },
    mounted(){
      this.initPage()
    },
    methods: {
      initPage() {
        this.sentimentId = this.$route.query.sentimentId
        this.getPublicSentimentDetail()
      },
      getPublicSentimentDetail() {
        this.Lshow = true;
        this.api.Post('publicSentimentDetail',this.qs({sentimentId:this.sentimentId})).then(res=>{
          this.publicSentimentDetailInfo = res.data
          this.Lshow = false;
      }).catch(res=>{
          console.log(res)
      })
      },
      bookClosed() {
        this.myDialogShow = false
        this.medalShow = false
      },
      bookClick() {
        this.myDialogShow = true
        this.medalShow = true
      },
      goBack() {
        this.$router.go(-1)
      }
    },
    components:{
      noData,
      loading
    }
  }
</script>
<style scoped lang="scss">
  .module-title{
    height: 14px;
    width: 200px;
    border-left: 2px solid #3da9f7;
    font-size: 16px;
    line-height: 14px;
    padding-left: 10px;
    color: #333333;
  }
  .title-box{
    overflow: hidden;
    line-height: 16px;
    margin-bottom: 20px;
  }
  .title-box span{
    font-size: 14px;
    cursor: pointer;
  }
  .title-box span:hover b{
    color: #1c9dfd;
  }
  .title-box span b{
    text-decoration: underline;
    font-weight: 400;
    color: #333333;
    margin-left: 3px;
  }
  .title-box span i{
    color: #1c9dfd;
  }
  .content-container{
    padding: 0 12px;
  }
 .content-title{
   height: 96px;
   border-bottom: 1px solid #dfdfdf;
   text-align: center;
 }
  .content-title h4{
    color: #333333;
    font-size: 22px;
    line-height: 22px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .content-title p{
    line-height: 50px;
    font-size: 14px;
  }
  .content-title p label{
    font-size: 14px;
    color: #999999;
    display: inline-block;
    vertical-align: middle;
  }
  .content-title p span{
    display: inline-block;
    vertical-align: middle;
    color: #999999;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: left;
  }
</style>



