<template>
  <div class="co-content">
    <h2 class="module-title" style="margin-bottom: 15px">所有商业角色</h2>
    <div class="container-item">
      <h3>法人<span>（{{total1}}）</span></h3>
      <noData v-if="noData1"></noData>
      <loading :Lshow.sync="Lshow1"></loading>
      <el-table
        v-if="!Lshow1"
        :data="tableData1"
        style="width: 100%">
        <el-table-column
          prop="idx"
          label="序号"
          width="90">
        </el-table-column>
        <el-table-column
          prop="entName"
          label="企业"
          width="290">
        </el-table-column>
        <el-table-column
          prop="area"
          label="地区"
          width="116">
        </el-table-column>
        <el-table-column
          prop="date"
          label="成立日期">
          width="145">
        </el-table-column>
        <el-table-column
          prop="money"
          label="注册资本">
          width="200">
        </el-table-column>
        <el-table-column
          prop="state"
          label="经营状态">
          width="110">
        </el-table-column>
        <el-table-column
          prop="warning"
          label="风险预警">
          width="110">
          <template slot-scope="scope">
            <i class="warn-icon" @click="warnClick()"></i>
          </template>
        </el-table-column>
      </el-table>
      <div v-if="!Lshow1" class="pagination-wrap cf">
        <el-pagination
          @current-change="handleCurrentChange1"
          :current-page="currentPage1"
          :page-size="pageSize1"
          layout="total, prev, pager, next, jumper"
          :total="total1"
        >
        </el-pagination>
        <el-button slot class="pagenation-btn">确定</el-button>
      </div>
    </div>
    <div class="container-item">
      <h3>股东<span>（{{total2}}）</span></h3>
      <noData v-if="noData2"></noData>
      <loading :Lshow.sync="Lshow2"></loading>
      <el-table
        v-if="!Lshow2"
        :data="tableData2"
        style="width: 100%">
        <el-table-column
          prop="idx"
          label="序号"
          width="90">
        </el-table-column>
        <el-table-column
          prop="entName"
          label="企业"
          width="290">
        </el-table-column>
        <el-table-column
          prop="hold"
          label="持股比例"
          width="100">
        </el-table-column>
        <el-table-column
          prop="area"
          label="地区"
          width="116">
        </el-table-column>
        <el-table-column
          prop="date"
          label="成立日期">
          width="145">
        </el-table-column>
        <el-table-column
          prop="money"
          label="注册资本">
          width="200">
        </el-table-column>
        <el-table-column
          prop="state"
          label="经营状态">
          width="110">
        </el-table-column>
        <el-table-column
          prop="warning"
          label="风险预警">
          width="110">
          <template slot-scope="scope">
            <i class="warn-icon" @click="warnClick()"></i>
          </template>
        </el-table-column>
      </el-table>
      <div v-if="!Lshow2" class="pagination-wrap cf">
        <el-pagination
          @current-change="handleCurrentChange2"
          :current-page="currentPage2"
          :page-size="pageSize2"
          layout="total, prev, pager, next, jumper"
          :total="total2"
        >
        </el-pagination>
        <el-button slot class="pagenation-btn">确定</el-button>
      </div>
    </div>
    <div class="container-item">
      <h3>高管<span>（{{total3}}）</span></h3>
      <noData v-if="noData3"></noData>
      <loading :Lshow.sync="Lshow3"></loading>
      <el-table
        v-if="!Lshow3"
        :data="tableData3"
        style="width: 100%">
        <el-table-column
          prop="idx"
          label="序号"
          width="90">
        </el-table-column>
        <el-table-column
          prop="entName"
          label="企业"
          width="290">
        </el-table-column>
        <el-table-column
          prop="position"
          label="职位"
          width="100">
        </el-table-column>
        <el-table-column
          prop="area"
          label="地区"
          width="116">
        </el-table-column>
        <el-table-column
          prop="date"
          label="成立日期">
          width="145">
        </el-table-column>
        <el-table-column
          prop="money"
          label="注册资本">
          width="200">
        </el-table-column>
        <el-table-column
          prop="state"
          label="经营状态">
          width="110">
        </el-table-column>
        <el-table-column
          prop="warning"
          label="风险预警">
          width="110">
          <template slot-scope="scope">
            <i class="warn-icon" @click="warnClick()"></i>
          </template>
        </el-table-column>
      </el-table>
      <div v-if="!Lshow3" class="pagination-wrap cf">
        <el-pagination
          @current-change="handleCurrentChange3"
          :current-page="currentPage3"
          :page-size="pageSize3"
          layout="total, prev, pager, next, jumper"
          :total="total3"
        >
        </el-pagination>
        <el-button slot class="pagenation-btn">确定</el-button>
      </div>
    </div>
    <el-dialog
      title="关联企业风险信息"
      :visible.sync="centerDialogVisible"
      width="650"
      custom-class="dialogClass"
      center>
      <div class="info-container">
        <p v-for="dangerItem in dangerouseList" @click="toDetail(dangerItem)">
          <span style="width: 120px" v-text="dangerItem.date"></span>
          <span style="width: 210px" v-text="dangerItem.result"></span>
          <label>案号：</label>
          <span style="width: 206px" v-text="dangerItem.cause"></span>
        </p>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import noData from './noData.vue'
  import loading from './loading.vue'
  export default{
    name: 'enterprise',
    data(){
      return {
        noData1: false,
        Lshow1: true,
        noData2: false,
        Lshow2: true,
        noData3: false,
        Lshow3: true,
        personId:'',
        currentPage1: 1,
        pageSize1: 8,
        total1: 0,
        centerDialogVisible:false,
        tableData1: [],
        currentPage2: 1,
        pageSize2: 8,
        total2: 0,
        tableData2: [],
        currentPage3: 1,
        pageSize3: 8,
        total3: 0,
        tableData3: [],
        dangerouseList:[]
      }
    },
    mounted(){
      this.initPage()
    },
    methods: {
      warnClick() {
        this.api.Post('dangerousInfo',this.qs({entId:this.entId})).then(res=>{
          this.dangerouseList = res.data
          this.centerDialogVisible = true
      }).catch(res=>{
          console.log(res)
      })
      },
      initPage() {
        this.personId = this.$route.query.personId
        this.getTableData1()
        this.getTableData2()
        this.getTableData3()
      },
      toDetail(item){
        this.$router.push({path:'/layout/detail/complaintDetail', query:{Id:item.id, typeName:'裁判文书'}})
      },
      getTableData1() {
        this.Lshow1 = true
        this.api.Post('legals',this.qs({
          personId:this.personId,
          pageSize: this.pageSize1,
          page:this.currentPage1
        })).then(res=>{
          this.total1 = res.total
        this.tableData1 = res.data.map(function(item, index) {
          return {
            "idx": index+1,
            "entName":item.entName,
            "area":item.area,
            "date":item.date,
            "money":item.money,
            "state":item.state,
            "warning":item.warning
          }
        })
       this.Lshow1 = false
      }).catch(res=>{
          console.log(res)
      })
      },
      getTableData2() {
        this.Lshow2 = true
        this.api.Post('holder',this.qs({
          personId:this.personId,
          pageSize: this.pageSize2,
          page:this.currentPage2
        })).then(res=>{
          this.total2 = res.total
        this.tableData2 = res.data.map(function(item, index) {
          return {
            "idx": index+1,
            "entName":item.entName,
            "hold":item.hold,
            "area":item.area,
            "date":item.date,
            "money":item.money,
            "state":item.state,
            "warning":item.warning
          }
        })
       this.Lshow2 = false
      }).catch(res=>{
          console.log(res)
      })
      },
      getTableData3() {
        this.Lshow3 = true
        this.api.Post('positons',this.qs({
          personId:this.personId,
          pageSize: this.pageSize3,
          page:this.currentPage3
        })).then(res=>{
          this.total3 = res.total
        this.tableData3 = res.data.map(function(item, index) {
          return {
            "idx": index+1,
            "entName":item.entName,
            "position":item.position,
            "area":item.area,
            "date":item.date,
            "money":item.money,
            "state":item.state,
            "warning":item.warning
          }
        })
        this.Lshow3 = false
      }).catch(res=>{
          console.log(res)
      })
      },
      handleCurrentChange1(val) {
        this.currentPage1 = val;
        this.getTableData1()
      },
      handleCurrentChange2(val) {
        this.currentPage2 = val;
        this.getTableData2()
      },
      handleCurrentChange3(val) {
        this.currentPage3 = val;
        this.getTableData3()
      }
    },
    components:{
      noData,
      loading
    }
  }
</script>
<style scoped lang="scss">
  .pagination-wrap{
    margin: 10px 0 5px 0;
  }
  .pagenation-btn{
    float: right;
    box-sizing: border-box;
    padding: 7px 12px;
  }
  .module-title{
    height: 14px;
    width: 200px;
    border-left: 2px solid #3da9f7;
    font-size: 16px;
    line-height: 16px;
    padding-left: 10px;
    color: #333333;
  }
.container-item{
  padding: 0 12px;
  margin-bottom: 16px;
}
  .container-item h3{
    line-height: 40px;
    font-size: 14px;
    color: #333333;
  }
  .container-item h3 span{
    color: #ea8416;
  }
  .warn-icon{
    display: inline-block;
    width: 16px;
    height: 16px;
    background: url("../assets/img/warn-icon_03.png");
    border-radius: 50%;
    margin-top: 10px;
    cursor: pointer;
  }
</style>
<style>
  .container-item  .el-table td>.cell{
    white-space: nowrap;
  }
 .container-item .el-table th{
    background-color: #fbfbfb;
  }
 .container-item  .el-table th>.cell{
    color: #333333;
    font-weight: 400;
    text-align: center;
    font-size: 14px;
  }
 .container-item  .el-table td>.cell{
    text-align: center;
    font-size: 14px;
  }
 .container-item  .el-table__footer-wrapper thead div, .container-item .el-table__header-wrapper thead div{
    background: none;
  }
.dialogClass{
  width: 650px;
  height: 240px;
  border-radius: 8px;
  overflow: hidden;
}
  .dialogClass  .el-dialog__header{
    background: #f0f0f0;
    height: 20px;
    padding: 18px 24px ;
    color: #000000;
    line-height: 20px;
    text-align: center;
    border-bottom: 1px solid #e0dddd;
  }
  .dialogClass  .el-dialog__header i{
    color: #333333;
  }
  .dialogClass .el-dialog__body{
    padding: 25px;
    overflow-y: scroll;
    height: 182px;
    box-sizing: border-box;
  }
  .info-container p{
    line-height: 30px;
    color: #000000;
    cursor: pointer;
    height: 30px;
    font-size: 14px;
  }
  .info-container p:hover{
    color: #4aa5ec;
  }
  .info-container p span{
    display: inline-block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: middle;
  }
  .info-container p label{
    display: inline-block;
    vertical-align: middle;
    width: 44px;
  }
</style>



