<template>
  <div class="co-content">
    <div class="title-box">
      <h3 class="module-title fl" v-text="typeName"></h3>
      <span class="fr" style="margin-right: 15px" @click="goBack"><i class="icon iconfont icon-fanhui"></i><b>返回列表</b></span>
    </div>
    <noData v-if="noData"></noData>
    <loading :Lshow.sync="Lshow"></loading>
    <div class="content-container" v-if="!Lshow">
      <div v-if="typeName == '法院公告'">
        <h4><span style="width: 984px" v-text="complaintDetailInfo.title"></span><span style="color: #999999; width: 90px; font-size: 14px"  v-text="complaintDetailInfo.date"></span><span class="type-box type-box1" @click="bookClick">法院公告</span></h4>
        <div class="content-box">
          <p>
            <label>公告类型：</label>
            <span v-text="complaintDetailInfo.noticeType"></span>
          </p>
          <p>
            <label>公告人：</label>
            <span v-text="complaintDetailInfo.announcer"></span>
          </p>
          <p>
            <label>公告时间：</label>
            <span v-text="complaintDetailInfo.announcementTime"></span>
          </p>
          <p>
            <label class="fl">公告内容：</label>
            <span class="fr" style="width: 1034px; overflow: auto; white-space: normal" v-text="complaintDetailInfo.announcementContent"></span>
          </p>
        </div>
      </div>
      <div v-if="typeName == '裁判文书'">
        <h4><label>案件名称：</label><span style="width: 900px" v-text="complaintDetailInfo.title"></span><span style="color: #999999; width: 90px; font-size: 14px"  v-text="complaintDetailInfo.date"></span><span class="type-box type-box2" @click="bookClick">裁判文书</span></h4>
        <div class="content-box">
          <p>
            <label>审理法院：</label>
            <span v-text="complaintDetailInfo.court"></span>
          </p>
          <p>
            <label>案件类型：</label>
            <span v-text="complaintDetailInfo.caseType"></span>
          </p>
          <p>
            <label>审判程序：</label>
            <span style="color: #ff6000" v-text="complaintDetailInfo.trialProcedure"></span>
          </p>
          <p>
            <label>案号：</label>
            <span v-text="complaintDetailInfo.caseNo"></span>
          </p>
          <p>
            <label>案由：</label>
            <span v-text="complaintDetailInfo.caseReason"></span>
          </p>
          <p>
            <label>上诉人：</label>
            <span style="color: #ff6000" v-text="complaintDetailInfo.appellant"></span>
          </p>
          <p>
            <label>被上诉人：</label>
            <span style="color: #ff6000" v-text="complaintDetailInfo.appellanted"></span>
          </p>
          <p>
            <label>适用法律：</label>
            <span v-text="complaintDetailInfo.applicableLaw"></span>
          </p>
          <p>
            <label>判决结果摘要：</label>
            <span v-html="complaintDetailInfo.caseResult"></span>
          </p>
          <p>
            <label>处理结果：</label>
            <span v-text="complaintDetailInfo.processingResults"></span>
          </p>
          <p>
            <label>裁判日期：</label>
            <span v-text="complaintDetailInfo.caseDate"></span>
          </p>
          <p>
            <label>前置文书： </label>
            <span style="color: #2382ff; text-decoration: underline" v-text="complaintDetailInfo.adjudicativeDocuments"></span>
          </p>
        </div>
      </div>
      <div v-if="typeName == '失信人'">
        <h4><span style="width: 984px" v-text="complaintDetailInfo.name"></span><span style="color: #999999; width: 90px; font-size: 14px"  v-text="complaintDetailInfo.date"></span><span class="type-box type-box3" @click="bookClick">失信人</span></h4>
        <div class="content-box">
          <p>
            <label>执行标的：</label>
            <span v-text="complaintDetailInfo.executes"></span>
          </p>
          <p>
            <label>立案时间：</label>
            <span v-text="complaintDetailInfo.filingTime"></span>
          </p>
          <p>
            <label>执行法院：</label>
            <span v-text="complaintDetailInfo.court"></span>
          </p>
          <p>
            <label>审判程序：</label>
            <span style="color: #ff7c42" v-text="complaintDetailInfo.trialProcedure"></span>
          </p>
          <p>
            <label>案号：</label>
            <span style="color: #5899ff" v-text="complaintDetailInfo.caseNo"></span>
          </p>
          <p>
          <label>案由：</label>
          <span v-text="complaintDetailInfo.caseCause"></span>
        </p>
          <p>
            <label>裁判日期：</label>
            <span v-text="complaintDetailInfo.refereeTime"></span>
          </p>
          <p>
            <label>原告：</label>
            <span v-text="complaintDetailInfo.plaintiff"></span>
          </p>
          <p>
            <label>被告：</label>
            <span v-text="complaintDetailInfo.defendant"></span>
          </p>
          <p>
            <label>第三人：</label>
            <span v-text="complaintDetailInfo.thirdPeople"></span>
          </p>
        </div>
      </div>
      <div v-if="typeName == '被执行人'">
        <h4><span style="width: 984px" v-text="complaintDetailInfo.name"></span><span style="color: #999999; width: 90px; font-size: 14px"  v-text="complaintDetailInfo.date"></span><span class="type-box type-box3" @click="bookClick">被执行人</span></h4>
        <div class="content-box">
          <p>
            <label>执行标的：</label>
            <span v-text="complaintDetailInfo.executes"></span>
          </p>
          <p>
            <label>立案时间：</label>
            <span v-text="complaintDetailInfo.filingTime"></span>
          </p>
          <p>
            <label>执行法院：</label>
            <span v-text="complaintDetailInfo.court"></span>
          </p>
          <p>
            <label>审判程序：</label>
            <span style="color: #ff7c42" v-text="complaintDetailInfo.trialProcedure"></span>
          </p>
          <p>
            <label>案号：</label>
            <span style="color: #5899ff" v-text="complaintDetailInfo.caseNo"></span>
          </p>
          <p>
            <label>案由：</label>
            <span v-text="complaintDetailInfo.caseCause"></span>
          </p>
          <p>
            <label>裁判日期：</label>
            <span v-text="complaintDetailInfo.refereeTime"></span>
          </p>
          <p>
            <label>原告：</label>
            <span v-text="complaintDetailInfo.plaintiff"></span>
          </p>
          <p>
            <label>被告：</label>
            <span v-text="complaintDetailInfo.defendant"></span>
          </p>
        </div>
      </div>
    </div>
    <div class="myDialog" v-if="myDialogShow">
      <div class="dialog-title">
        <h3 class="module-title fl">裁判文书</h3>
        <i class="icon iconfont icon-guanbi fr" @click="bookClosed"></i>
      </div>
      <div class="dialog-content">
        <img src="../assets/img/example_03.png" alt="">
      </div>
    </div>
    <div class="my-medal" v-if="medalShow" @click="bookClosed"></div>
  </div>
</template>
<script>
  import noData from './noData.vue'
  import loading from './loading.vue'
  export default{
    name: 'complaintDetail',
    data(){
      return {
        noData: false,
        Lshow: true,
        myDialogShow: false,
        medalShow:false,
        id:'',
        typeName:'',
        complaintDetailInfo:{}
      }
    },
    mounted(){
      this.initPage()
    },
    methods: {
      initPage() {
        this.id = this.$route.query.id
        this.typeName = this.$route.query.typeName
        if(this.typeName == '法院公告'){
          this.getComplaintCourtDetail()
        }
        if(this.typeName == '裁判文书'){
          this.getComplaintRefereeDetail()
        }
        if(this.typeName == '失信人'){
          this.getComplaintDishonestyDetail()
        }
        if(this.typeName == '被执行人'){
          this.getComplaintExecutorDetail()

        }
      },
      getComplaintRefereeDetail() {
          this.Lshow = true
        this.api.Post('complaintRefereeDetail',this.qs({id:this.id})).then(res=>{
          this.complaintDetailInfo = res.data
          this.Lshow = false
      }).catch(res=>{
          console.log(res)
      })
      },
      getComplaintCourtDetail() {
        this.Lshow = true
        this.api.Post('complaintCourtDetail',this.qs({id:this.id})).then(res=>{
          this.complaintDetailInfo = res.data
        this.Lshow = false
      }).catch(res=>{
          console.log(res)
      })
      },
      getComplaintDishonestyDetail() {
        this.Lshow = true
        this.api.Post('complaintDishonestyDetail',this.qs({id:this.id})).then(res=>{
          this.complaintDetailInfo = res.data
        this.Lshow = false
      }).catch(res=>{
          console.log(res)
      })
      },
      getComplaintExecutorDetail() {
        this.Lshow = true
        this.api.Post('complaintExecutorDetail',this.qs({id:this.id})).then(res=>{
          this.complaintDetailInfo = res.data
        this.Lshow = false
      }).catch(res=>{
          console.log(res)
      })
      },
      bookClosed() {
        this.myDialogShow = false
        this.medalShow = false
      },
      bookClick() {
        this.myDialogShow = true
        this.medalShow = true
      },
      goBack() {
        this.$router.go(-1)
      }
    },
    components:{
      noData,
      loading
    }
  }
</script>
<style scoped lang="scss">
  .module-title{
    height: 14px;
    width: 200px;
    border-left: 2px solid #3da9f7;
    font-size: 16px;
    line-height: 14px;
    padding-left: 10px;
    color: #333333;
  }
  .title-box{
    overflow: hidden;
    line-height: 16px;
    margin-bottom: 20px;
  }
  .title-box span{
    font-size: 14px;
    cursor: pointer;
  }

  .title-box span:hover b{
    color: #1c9dfd;
  }
  .title-box span b{
    text-decoration: underline;
    font-weight: 400;
    color: #333333;
    margin-left: 3px;
  }
  .title-box span i{
    color: #1c9dfd;
  }
  .content-container{
    padding: 0 12px;
  }
.content-container h4{
  line-height: 56px;
  color: #2c9aea;
  font-size: 16px;
  border-bottom: 1px solid #dfdfdf;
  font-weight: 400;
  margin-bottom: 10px;
}
  .content-container h4 label{
    display: inline-block;
  }
  .content-container h4 span{
    display: inline-block;
  }
  .content-container h4 .type-box{
    display: inline-block;
    width: 60px;
    height: 20px;
    line-height: 18px;
    font-size: 12px;
    text-align: center;
    box-sizing: border-box;
    vertical-align: text-bottom;
    border-radius: 3px;
    cursor: pointer;
  }
  .content-container h4 .type-box:hover{
    opacity: .7;
  }
  .content-container h4 .type-box1{
    background: #f0f9ff;
    color: #79c3fb;
    border: 1px solid #79c3fb;
  }
  .content-container h4 .type-box2{
    background: #f3ffef;
    color: #3bb624;
    border: 1px solid #3bb624;
  }
  .content-container h4 .type-box3{
    background: #fff6f3;
    color: #ed5f45;
    border: 1px solid #ed5f45;
  }
  .content-container h4 .type-box4{
    background: #fffaee;
    color: #d98d06;
    border: 1px solid #d98d06;
  }
  .content-box p{
    line-height: 34px;
    font-size: 14px;
    color: #999999;
    overflow: hidden;
  }
  .content-box p label{
       display: inline-block;
       width: 100px;
      vertical-align: top;
     }
  .content-box p span{
    display: inline-block;
    vertical-align: top;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
.my-medal{
  position: fixed;
  right: 0;
  bottom: 0;
  left: 0;
  top: 0;
  background: #000000;
  opacity: .5;
}
  .myDialog{
    width: 1100px;
    position: fixed;
    z-index: 99999;
    top:5%;
    left: 0;
    right: 0;
    bottom: 5%;
    margin: auto;
    background: #ffffff;
    overflow: hidden;
    box-shadow: 0 0 10px #e2e2e2;
  }
  .myDialog .dialog-title{
    height: 60px;
    background: #f3f3f3;
    line-height: 60px;
  }
  .myDialog .module-title{
    margin: 22px 0 0 19px;
  }
  .myDialog .dialog-title i{
    line-height: 60px; margin-right: 20px; cursor: pointer;
  }
  .myDialog .dialog-title i:hover{
    opacity: .7;
  }
  .dialog-content{
    position: absolute;
    width: 100%;
    height: calc(100% - 60px);
    padding: 108px 0;
    box-sizing: border-box;
    overflow: auto;
  }
  .dialog-content img{
    margin: auto;
    margin-left: 160px;
  }
</style>



