import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Layout from '@/pages/layout'
import SearchList from '@/pages/searchList'
import Detail from '@/pages/detail'
import Complaint from '@/components/complaint'
import ComplaintDetail from '@/components/complaintDetail'
import PublicSentiment from '@/components/publicSentiment'
import PublicSentimentDetail from '@/components/publicSentimentDetail'
import Enterprise from '@/components/enterprise'
import Personage from '@/components/personage'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      mate: {
        keepAlive: true
      }
    },
    {
      path: '/layout',
      name: 'layout',
      component: Layout,
      children:[
        {
          path: '',
          name: 'searchList',
          component: SearchList,
        },
        {
          path: 'detail',
          name: 'detail',
          component: Detail,
          children:[
            {
              path: '',
              name: 'complaint',
              component: Complaint,
            },
            {
              path: 'complaintDetail',
              name: 'complaintDetail',
              component: ComplaintDetail,
            },
            {
              path: 'publicSentiment',
              name: 'publicSentiment',
              component: PublicSentiment,
            },{
              path: 'publicSentimentDetail',
              name: 'publicSentimentDetail',
              component: PublicSentimentDetail,
            },
            {
              path: 'enterprise',
              name: 'enterprise',
              component: Enterprise,
            },
            {
              path: 'personage',
              name: 'personage',
              component: Personage,
            }
          ]
        }
      ],
      mate: {
        keepAlive: true
      }
    }
  ]
})



