<template>
  <div class="co-content">
    <div class="co-content-left fl">
      <div class="chart-box chart-box1">
        <h3 class="module-title" style="margin-top: 20px;margin-bottom: 15px">历年涉诉情况</h3>
        <div class="chart-box1-container" id="chart1"></div>
      </div>
      <div class="chart-box chart-box2">
        <h3 class="module-title" style="margin-top: 20px;margin-bottom: 15px">标的金额分布</h3>
        <div class="chart-box2-container">
          <div class="time-container">
            <div class="time-item" v-for="moneyItem in moneyList">
              <img src="../assets/img/timeline_03.png" alt="">
              <span class="money-text"><label v-text="moneyItem.count"></label>万</span>
              <span class="icon-box"></span>
              <span class="time-text" v-text="moneyItem.year"></span>
            </div>
          </div>
        </div>
        <p class="total-money">总金额：<label v-text="totalMoney"> </label><span>万元</span></p>
      </div>
      <div class="chart-box chart-box3">
        <h3 class="module-title" style="margin-top: 20px;margin-bottom: 15px">案由</h3>
        <div class="chart-box3-container" id="chart3"></div>
      </div>
      <div class="chart-box chart-box4">
        <h3 class="module-title" style="margin-top: 20px;margin-bottom: 15px">涉诉方</h3>
        <div class="chart-box4-container" id="chart4"></div>
      </div>
    </div>
    <div class="co-content-right fr">
      <div class="title">
        <h3 class="module-title fl" style="margin-top: 12px">涉诉信息列表</h3>
        <div class="child-tab-box">
          <span v-for="(tabItem, idx) in childTabs" :class="{'active':idx == currentIdx}" v-text="tabItem" @click="tabClick(idx, tabItem)"></span>
        </div>
      </div>
      <noData v-if="noData"></noData>
      <loading :Lshow.sync="Lshow"></loading>
      <div class="tab-content" v-if="!Lshow">
        <ul>
          <li v-for="complaintItem in complaintList" @click="listClick(complaintItem)">
            <div v-if="complaintItem.type=='法院公告'">
              <div class="tabItem-title">
                <h4 v-text="complaintItem.title"></h4><span class="btnStyle1">法院公告</span>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">公告类型：</span><span style="width: 658px; height: 26px; color: #666666" v-text="complaintItem.noticeType"></span></p>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">公告人：</span><span style="width: 180px; height: 26px; color: #666666"  v-text="complaintItem.announcer"></span><span style="width: 70px; height: 26px">公告时间：</span><span style="width: 195px; height: 26px; color: #666666"   v-text="complaintItem.announcementTime"></span> </p>
              </div>
            </div>
            <div v-if="complaintItem.type=='裁判文书'">
              <div class="tabItem-title">
                <h4 v-text="complaintItem.title"></h4><span class="btnStyle2">裁判文书</span>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">处理结果：</span><span style="width: 658px; height: 26px; color: #666666" v-text="complaintItem.processingResults"></span></p>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">发布日期：</span><span style="width: 100px; height: 26px; color: #666666"  v-text="complaintItem.releaserDate"></span><span style="width: 70px; height: 26px">审理法院：</span><span style="width: 195px; height: 26px; color: #666666"   v-text="complaintItem.trialCourt"></span>    <span style="width: 42px; height: 26px">案号：</span><span style="width: 250px; height: 26px; color: #666666"   v-text="complaintItem.caseNo"></span> </p>
              </div>
            </div>
            <div v-if="complaintItem.type=='失信人'">
              <div class="tabItem-title">
                <h4 v-text="complaintItem.name"></h4><span class="btnStyle3">失信人</span>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">执行标的：</span><span style="width: 658px; height: 26px; color: #666666" v-text="complaintItem.Executes"></span></p>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">发布日期：</span><span style="width: 100px; height: 26px; color: #666666"  v-text="complaintItem.releaserDate"></span><span style="width: 70px; height: 26px">审理法院：</span><span style="width: 195px; height: 26px; color: #666666"   v-text="complaintItem.trialCourt"></span>    <span style="width: 42px; height: 26px">案号：</span><span style="width: 250px; height: 26px; color: #666666"   v-text="complaintItem.caseNo"></span> </p>
              </div>
            </div>
            <div v-if="complaintItem.type=='被执行人'">
              <div class="tabItem-title">
                <h4 v-text="complaintItem.name"></h4><span class="btnStyle4">被执行人</span>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">执行标的：</span><span style="width: 658px; height: 26px; color: #666666" v-text="complaintItem.Executes"></span></p>
              </div>
              <div class="tabItem-content-item">
                <p><span style="width: 70px; height: 26px">发布日期：</span><span style="width: 100px; height: 26px; color: #666666"  v-text="complaintItem.releaserDate"></span><span style="width: 70px; height: 26px">审理法院：</span><span style="width: 195px; height: 26px; color: #666666"   v-text="complaintItem.trialCourt"></span>    <span style="width: 42px; height: 26px">案号：</span><span style="width: 250px; height: 26px; color: #666666"   v-text="complaintItem.caseNo"></span> </p>
              </div>
            </div>
          </li>
        </ul>
      </div>
      <div class="pagination-wrap cf" v-if="pageShow">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next, jumper"
          :total="total"
        >
        </el-pagination>
        <el-button slot class="pagenation-btn">确定</el-button>
      </div>
    </div>
  </div>
</template>
<script>
  import noData from './noData.vue'
  import loading from './loading.vue'
  import {complaintPie, causeBar, yearBar} from '../assets/js/echartsComponents'
  export default{
    name: 'complaint',
    data(){
      return {
        noData: false,
        Lshow: true,
        personId:'',
        currentIdx: 0,
        childTabs:['全部', '法院公告', '裁判文书', '失信人', '被执行人'],
        moneyList:[],
        totalMoney:0,
        complaintList:[],
        currentPage: 1,
        pageSize: 8,
        total: 0,
        pageShow:true
      }
    },
    mounted(){
      this.initPage()
    },
    methods: {
      listClick(item) {
        this.$router.push({path:'/layout/detail/complaintDetail', query:{Id:item.id, typeName:item.type}})
      },
      tabClick(idx, typename) {
        this.currentIdx = idx
        this.currentPage = 1
        this.getComplaintList()
      },
      initPage() {
        this.personId = this.$route.query.personId
        this.getYearBar()
        this.getMoneyList()
        this.getCauseBar()
        this.getComplaintPie()
        this.getComplaintList()
      },
      getYearBar() {
        this.api.Post('yearList',this.qs({personId:this.personId})).then(res=>{
          yearBar("chart1", res.data)
      }).catch(res=>{
          console.log(res)
      })
      },
      getMoneyList() {
        let _this = this
        this.api.Post('moneyList',this.qs({personId:this.personId})).then(res=>{
          res.data.forEach(function(item) {
          _this.totalMoney +=item.count
        })
        this.moneyList = res.data
          document.querySelector(".time-container").style.width = res.data.length*110 + 'px'
      }).catch(res=>{
          console.log(res)
      })
      },
      getCauseBar() {
        this.api.Post('causeBar',this.qs({personId:this.personId})).then(res=>{
          causeBar("chart3", res.data)
        }).catch(res=>{
            console.log(res)
        })
      },
      getComplaintPie() {
        this.api.Post('getComplainPie',this.qs({personId:this.personId})).then(res=>{
          complaintPie("chart4", res.data)
        }).catch(res=>{
            console.log(res)
        })
      },
      getComplaintList() {
        this.Lshow = true;
        this.pageShow = false
        let _this = this
          this.api.Post('complaintList',this.qs({
            personId:this.personId,
            typename:this.childTabs[_this.currentIdx],
            pageSize: this.pageSize,
            page:this.currentPage
          })).then(res=>{
            console.log(res.data[0].type)
            this.total = res.total
            this.complaintList = res.data
            this.Lshow = false;
            this.pageShow  = true
        if (_this.complaintList <= 0) {
          _this.noData = true;
          _this.pageShow  = false
        }
        }).catch(res=>{
            console.log(res)
        })
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.getComplaintList();
      }
    },
    components:{
      noData,
      loading
    }
  }
</script>
<style scoped lang="scss">
  .pagenation-btn{
    float: right;
    box-sizing: border-box;
    padding: 7px 12px;
    margin-right: 22px;
  }
  .co-content{
    overflow: hidden;
  }
  .module-title{
    height: 14px;
    width: 200px;
    border-left: 2px solid #3da9f7;
    font-size: 16px;
    line-height: 16px;
    margin-left: 20px;
    padding-left: 10px;
    color: #333333;
  }
  .co-content-left{
    width: 351px;
    box-sizing: border-box;
  }
  .chart-box{
    border: 1px solid #dbdbdb;
    box-sizing: border-box;
    margin-bottom: 20px;
  }
  .chart-box1{
    height: 278px;
  }
  .chart-box2{
    height: 278px;
  }
  .chart-box3{
    height: 396px;
  }
  .chart-box4{
    height: 319px;
  }
  .chart-box1-container{
    height: 226px;
  }
  .chart-box2-container{
    width: 290px;
    height: 165px;
    margin-left: 30px;
    margin-top: 20px;
    overflow: hidden;
  }
  .time-container{
    height: 145px;
    margin-left: -17px;
  }
  .time-item{
    width: 100px;
    height: 101px;
    float: left;
    margin: 0 5px;
    margin-top: 46px;
    position: relative;
  }
  .time-item img{
    position: absolute;
    left: 0;
  }
  .time-item:nth-child(even) img{
    bottom: 26px;
  }
  .time-item:nth-child(odd) img{
    transform: rotate(180deg);
    top: 26px;
  }
  .money-text{
    display: inline-block;
    height: 26px;
    width: 64px;
    position: absolute;
    background: #4dc1f9;
    color: #ffffff;
    text-align: center;
    line-height: 26px;
    font-size: 16px;
    border-radius: 3px;
    left:17px;
  }
  .time-item:nth-child(odd) .money-text{
    top: 0;
  }
  .time-item:nth-child(even) .money-text{
    bottom: 0;
  }

  .time-text{
    display: inline-block;
    position: absolute;
    color: #333333;
    font-size: 12px;
    left: 36px;
  }
  .time-item:nth-child(odd) .time-text{
    top: 58px;
    right: 0;
  }
  .time-item:nth-child(even) .time-text{
    bottom: 58px;
  }
  .icon-box{
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background: #4dc1f9;
    position: absolute;
    right: -8px;
    top: 48px;
  }
  .time-item:last-child .icon-box{
    display: none;
  }
  .total-money{
    color: #3d3d3d;
    font-size: 14px;
    line-height: 30px;
    padding-left: 30px;
    margin-top: 5px;
  }
  .total-money label{
    color: #ed9649;
    font-size: 16px;
  }
  .total-money span{
    color: #ed9649;
    font-size: 12px;
  }
  .chart-box3-container{
    height: 345px;
  }
  .chart-box4-container{
    height: 268px;
  }
  .co-content-right{
    width: 795px;
    border: 1px solid #dbdbdb;
    box-sizing: border-box;
  }
  .co-content-right .title{
   height: 38px;
   border-bottom: 1px solid #dbdbdb;
   background: #fbfbfb;
  }
.child-tab-box{
  width: 435px;
  height: 38px;
  float: right;
}
.child-tab-box span{
  display: block;
  width: 77px;
  height: 28px;
  box-sizing: border-box;
  border: 1px solid #dbdbdb;
  float: left;
  margin-right: 10px;
  margin-top: 5px;
  font-size: 14px;
  color: #666666;
  line-height: 26px;
  text-align: center;
  cursor: pointer;
}
  .child-tab-box span:hover{
    color: #2c9aea;
  }
  .child-tab-box span.active{
    color: #ffffff;
    background: #2c9aea;
    border: 1px solid #2c9aea;
  }
  .tab-content{
    padding: 0 20px;
  }
  .tab-content ul li{
    height: 126px;
    border-bottom: 1px dashed #dbdbdb;
    padding: 12px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .tab-content ul li:last-child{
    border-bottom: 1px solid #dbdbdb;
  }
  .tabItem-title{
    line-height: 44px;
    height: 44px;
    position: relative;
  }
  .tabItem-title h4{
    padding-right: 100px;
    color: #333333;
    font-size: 16px;
    font-weight: 400;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    cursor: pointer;
    vertical-align: middle;
  }
  .tabItem-title h4:hover{
    color: #2c9aea;
    text-decoration: underline;
  }
  .tabItem-title span{
    display: inline-block;
    position: absolute;
    width: 60px;
    height: 20px;
    line-height: 18px;
    font-size: 12px;
    text-align: center;
    box-sizing: border-box;
    vertical-align: middle;
    border-radius: 3px;
    right: 0;
    top: 12px;
  }
  .tabItem-title span.btnStyle1{
    background: #f0f9ff;
    color: #79c3fb;
    border: 1px solid #79c3fb;
  }
  .tabItem-title span.btnStyle2{
    background: #f3ffef;
    color: #3bb624;
    border: 1px solid #3bb624;
  }
  .tabItem-title span.btnStyle3{
    background: #fff6f3;
    color: #ed5f45;
    border: 1px solid #ed5f45;
  }
  .tabItem-title span.btnStyle4{
    background: #fffaee;
    color: #d98d06;
    border: 1px solid #d98d06;
  }
  .tabItem-content-item p{
    font-size: 14px;
    color: #999999;
    line-height: 26px;
  }
  .tabItem-content-item p span{
    display: inline-block;
    float: left;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  ::-webkit-scrollbar{width:5px;height:5px;}
  ::-webkit-scrollbar-thumb{ background:rgba(204,204,204,1); border-radius: 5px;}
</style>



