<template>
  <div class="co-content">
    <h2 class="module-title" style="margin-bottom: 30px">马化腾的关系图谱</h2>
    <div class="tool_tip" id="tool_tip" v-show="toolShow">
      <h3>{{toolData.name}}<span><img src="../assets/img/map-icon-d_03.png" alt=""></span></h3>
      <p>职位：{{toolData.myPosition}}</p>
      <p>负面信息：涉诉<span>({{toolData.mycomplaintNum}})</span>、负面舆情<span>({{toolData.mypublicSentimentNum}})</span></p>
      <i></i>
    </div>
    <noData v-if="noData"></noData>
    <loading :Lshow.sync="Lshow"></loading>
    <div class="container-content" v-if="!Lshow">
      <div class="content-item" v-for="(item, index) in relationsList">
        <h3><label v-text="item.entName"></label><span>（{{item.position}}）</span></h3>
        <div class="chart-container" :id="'chart'+index"></div>
      </div>
    </div>
  </div>
</template>
<script>
  import noData from './noData.vue'
  import loading from './loading.vue'
  import {relationMap} from '../assets/js/echartsComponents'
  export default{
    name: 'personage',
    data(){
      return {
        noData: false,
        Lshow: true,
        toolShow:false,
        toolData:{},
        relationsList:[]
      }
    },
    mounted(){
      this.initPage()
    },
    methods:{
      initPage() {
        this.initChart()
      },
      initChart() {
        this.Lshow = true
        this.api.Post('relationsMap',this.qs({personId:this.personId})).then(res=>{
          let it = this
          this.Lshow = false
          this.relationsList =res.data
         res.data.forEach(function(item, index){
           let myId = 'chart'+index

           it.$nextTick(function () {
             relationMap(myId, item, it)
             relationMap(myId, item, it)
             relationMap(myId, item, it)
           })
         })
      }).catch(res=>{
          console.log(res)
      })
      }
    },
    components:{
      noData,
      loading
    }
  }
</script>
<style scoped lang="scss">
  .module-title{
    height: 14px;
    width: 200px;
    border-left: 2px solid #3da9f7;
    font-size: 16px;
    line-height: 16px;
    padding-left: 10px;
    color: #333333;
  }
  .container-content{
    min-height: 700px;
  }
  .tool_tip {
    position: absolute;
    width: 230px;
    height: 94px;
    border: 1px solid #ea3430;
    left: 800px;
    top: 250px;
    background: #fffcfc;
    z-index: 10000;
    padding: 8px 18px;
    border-radius: 5px;
    box-sizing: border-box;
    box-shadow: 0 0 10px #d8d4d4;
  }
  .tool_tip i{
    display: inline-block;
    width: 22px;
    height: 9px;
    background: url("../assets/img/tooltip-icon_03.png");
    position: absolute;
    left: 30px;
    bottom: -9px;
  }
  .tool_tip h3{
    line-height: 32px;
    font-size: 16px;
    color: #333333;
  }
  .tool_tip h3 span{
    float: right;
    margin-top: 8px;
  }
  .tool_tip p{
    line-height: 22px;
    color: #494949;
    font-size: 12px;
  }
  .tool_tip p span{
    color: #ea8416;
  }

  .content-item{
    overflow: hidden;
  }
  .content-item h3{
    line-height: 30px;
    padding-left: 30px;
    font-size: 14px;
    color: #333333;
    font-weight: 400;
  }
  .chart-container{
    height: 400px;
  }
</style>



