import echarts from 'echarts'

export default function sentimentLine(id, data) {
  let yearArr = []
  let valueArr = []
  data.forEach(function(item) {
    yearArr.push(item.year)
    valueArr.push(item.count)
  })
  let myChart = echarts.init(document.getElementById(id))
  let option = {
    grid: {
      left: '15',
      right: '30',
      bottom: '30',
      top:'15',
      containLabel: true
    },
    tooltip : {
      trigger: 'axis',
      axisPointer: {
        animation: false
      }
    },
    legend: {
      //data:['流量','降雨量'],
      //x: 'left'
    },
    xAxis : [
      {
        type : 'category',
        boundaryGap : false,
        axisTick: {show: false},
        axisLabel:{
          textStyle:{
            color:'#333333'
          }
        },
        axisLine: {
          lineStyle: {
            color: "#dbdbdb"
          }
        },
        data : yearArr.map(function (str) {
          return str.replace(' ', '\n')
        })
      }
    ],
    yAxis: [
      {
        //name: '流量(m^3/s)',
        type: 'value',
        axisTick: {show: false},
        axisLine: {
          lineStyle: {
            color: "#dbdbdb"
          }
        },
        axisLabel:{
          textStyle:{
            color:'#333333'
          }
        },
        splitArea:{
          show:true,
          areaStyle:{
            color:['#fbfbfb','#ffffff']
          }

        }
        //max: 12000
      }
    ],
    series: [
      {
        name:'负面舆论',
        type:'line',
        animation: true,
        smooth:'true',
        symbolSize:0,
        areaStyle: {
          normal: {
            color:'#dff4ff',
          }
        },
        symbolSize: 10,
        symbol:'circle',
        itemStyle: {
          normal: {
            color: '#3da9f7',
            borderWidth:2,
            borderColor:'#ffffff'
          },
          emphasis: {
            borderColor:'#ffffff'
          }
        },
        lineStyle: {
          normal: {
            width: 2,
            color:'#41abf7',
          }
        },
        markArea: {
          silent: true,
          data: [[{
            xAxis: '2009/9/12\n7:00'
          }, {
            xAxis: '2009/9/22\n7:00'
          }]]
        },
        data:valueArr
      }
    ]
  };
  myChart.setOption(option)
}





