import echarts from 'echarts'

export default function complaintPie(id, data) {
  let myChart = echarts.init(document.getElementById(id))
  let option = {
    tooltip: {
      show: true,
      formatter: function(params) {
        if (params.seriesIndex != "0" && params.seriesIndex != "1" && params.seriesIndex != "2") {
          return "{a} <br/>{b} : {c} ({d}%)";
        }
      }
    },
    backgroundColor:'#fff',
    //legend:{
    //  x:20,
    //  y2:20,
    //  orient: 'vertical',
    //  data:["铁路运输业","公路运输业","航空运输业","港口运输业"]
    //},
    color: ['#3abbf0','#f0ae3a','#2d8fe3','#f23d22','#e86d12'],
    calculable: false,
    series: [{
      // name: '注销企业数量',
      type: 'pie',
      center: ['50%', '45%'],
      radius: [40, 60],
      label: {
        normal: {
          position: 'outside',
          show: true,
          formatter: "{d}%\n{b}",
          textStyle: {
            color: "#666666",
            fontSize:12
          }
        }
      },
      labelLine: {
        normal: {
          textStyle: {
            fontSize: 10
          },
          length: 14,
          length2: 14,
          lineStyle: {
            width: 1
          }
        }
      },
      data: data
    }]
  };
  myChart.setOption(option)
}





