<template>
  <div class="content">
    <div class="content-top">
      <div class="personItem fl">
        <img :src="detailInfo.purl" alt="">
        马化腾
      </div>
      <div class="content-top-item fl">
        <h3 style="color: #ea8416">
          <label v-text="detailInfo.complaint"></label>
          <span>条</span>
        </h3>
        <p>涉诉信息</p>
      </div>
      <div class="content-top-item fl">
        <h3 style="color: #e95037">
          <label v-text="detailInfo.negative"></label>
          <span>条</span>
        </h3>
        <p>负面舆情</p>
      </div>
      <div class="content-top-item fl">
        <h3 style="color: #41b431">
          <label v-text="detailInfo.ent"></label>
          <span>家</span>
        </h3>
        <p>风险关联企业</p>
      </div>
      <div class="content-top-item fl" style="background: none">
        <h3 style="color: #2c9aea">
          <label v-text="detailInfo.personcont"></label>
          <span>位</span>
        </h3>
        <p>风险关联人物</p>
      </div>
      <div class="evaluateItem fl">
        <div class="evaluateItem-icon-top"></div>
        <div class="evaluateItem-icon-bottom"></div>
        <div v-if="detailInfo.state == '1'">
          <div class="fl" style="width: 40px; margin-right: 10px">
            <img src="../assets/img/detail-icon_03.png" alt="">
          </div>
          <div class="fr" style="width: 65px">
            <h4 style="font-size: 16px; color: #79b9f0; line-height: 20px">一般</h4>
            <p style="font-size: 14px; color: #333333; line-height: 20px">负面等级</p>
          </div>
        </div>
        <div v-if="detailInfo.state == '2'">
          <div class="fl" style="width: 40px; margin-right: 10px">
            <img src="../assets/img/detail-icon_05.png" alt="">
          </div>
          <div class="fr" style="width: 65px">
            <h4 style="font-size: 16px; color: #21b821; line-height: 20px">良好</h4>
            <p style="font-size: 14px; color: #333333; line-height: 20px">正面等级</p>
          </div>
        </div>
        <div v-if="detailInfo.state == '3'">
          <div class="fl" style="width: 40px; margin-right: 10px">
            <img src="../assets/img/detail-icon_07.png" alt="">
          </div>
          <div class="fr" style="width: 65px">
            <h4 style="font-size: 16px; color: #fad093; line-height: 20px">极差</h4>
            <p style="font-size: 14px; color: #333333; line-height: 20px">负面等级</p>
          </div>
        </div>
      </div>
    </div>
    <div class="content-bottom">
      <div class="tab-title">
        <span v-for="(tabItem, idx) in tabTitles" :class="{'active': currentIdx == idx}" v-text="tabItem.name" @click="tabClick(idx,tabItem)">涉诉信息</span>
      </div>
      <div class="tab-content">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    name: 'detail',
    data(){
      return {
        detailInfo:{},
        personId:'',
        tabTitles:[
          {
          name:'涉诉信息',
          path:'/layout/detail'
          },
          {
            name:'舆情信息',
            path:'/layout/detail/publicSentiment'
          },
          {
            name:'关联企业',
            path:'/layout/detail/enterprise'
          },
          {
            name:'关联人物',
            path:'/layout/detail/personage'
          }
        ],
        currentIdx: 0,
      }
    },
    mounted(){
      this.initPage()
    },
    activated() {
    },
    methods: {
      tabClick(idx, item) {
        this.currentIdx = idx
        this.$router.push({path:item.path, query:{personId:this.personId}})
      },
      initPage() {
        this.getDetail()
      },
      getDetail() {
        this.personId = this.$route.query.personId
        this.api.Post('detailInfo',this.qs({personId:this.personId})).then(res=>{
          this.detailInfo = res.data
      }).catch(res=>{
          console.log(res)
      })
      }
    }
  }
</script>

<style scoped lang="scss">
  .content{
    width: 1200px;
    margin: auto;
  }
  .content-top{
    height: 162px;
    margin: 16px 0;
    background: #ffffff;
    border: 1px solid #d7d7d7;
    box-sizing: border-box;
  }
  .personItem{
    width: 242px;
    height: 160px;
    background: #f0f7ff;
    box-shadow: rgba(217, 217, 217, .8) 5px 0px 10px;
    padding: 40px 30px;
    box-sizing: border-box;
    font-size: 20px;
    color: #333333;
    font-weight: 700;
    line-height: 80px;
  }
  .personItem img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin-right: 20px;
  }
  .content-top-item{
    width: 185px;
    height: 160px;
    background: url("../assets/img/detail-t_07.png") no-repeat right;
    padding-top: 50px;
    box-sizing: border-box;
    text-align: center;
  }
  .content-top-item h3{
    line-height: 42px;
    color: #e95037;
    font-weight: 400;
    font-size: 24px;
  }
  .content-top-item span{
    font-size: 14px;
    margin-left: -6px;
    margin-top: 10px;
    line-height: 14px;
  }
  .content-top-item p{
    line-height: 40px;
    color: #333333;
    font-size: 14px;
  }
  .evaluateItem{
    height: 160px;
    border-left: 1px dashed #dbdbdb;
    box-sizing: border-box;
    position: relative;
    padding-top: 60px;
    padding-left: 55px;
  }
  .evaluateItem-icon-top{
    width: 26px;
    height: 9px;
    position: absolute;
    left: -14px;
    top: -1px;
    background: url("../assets/img/detail-t_03.png") no-repeat center;
  }
  .evaluateItem-icon-bottom{
    width: 26px;
    height: 9px;
    position: absolute;
    left: -14px;
    bottom: -1px;
    background: url("../assets/img/detail-t_03.png") no-repeat center;
    transform: rotate(180deg);
  }
  .content-bottom{
    border: 1px solid #dbdbdb;
    margin-bottom: 32px;
  }
  .tab-title{
    height: 38px;
    background: #f3f8fd;
    border-bottom: 1px solid #dbdbdb;
  }
  .tab-title span{
    display:block;
    float: left;
    width: 120px;
    line-height: 38px;
    font-size: 14px;
    color: #333333;
    text-align: center;
    border-right: 1px solid #dbdbdb;
    cursor: pointer;
  }
  .tab-title span.active{
    background: #ffffff;
    color: #16a7e3;
    line-height: 39px;
  }
  .tab-title span:hover{
    background: #ffffff;
    color: #16a7e3;
    line-height: 39px;
  }
  .tab-content{
    padding: 20px 20px 60px 20px;
    background: #ffffff;
  }
</style>



