import echarts from 'echarts'

export default function relationMap(id, data, it) {
  let myColors =['#0a9aff','#6cd52c','#ff9a23']
  let myDatas= []
  let myLinks = []
  let targetObj = ''
  data.relationsPersons.querySubjects.forEach(function(item){
     targetObj = item.personName
     let seriesObj = {
       name: item.personName,
       myPosition:item.position,
       mycomplaintNum:item.complaintNum,
       mypublicSentimentNum:item.publicSentimentNum,
       myseverityType:item.severityType,
       x:100,
       y:100,
       symbolSize: 14,
       myType:item.type,
       value:"75",
       itemStyle:{normal:{
         color:myColors[0],
         borderColor:'#0671f5',
         borderWidth: 1
       }}
     }
    myDatas.push(seriesObj)
  })

  data.relationsPersons.securitys.forEach(function(item){
    let linkObj = {
      source:item.personName,
      target:targetObj
    }
    let seriesObj = {
      name: item.personName,
      myPosition:item.position,
      mycomplaintNum:item.complaintNum,
      mypublicSentimentNum:item.publicSentimentNum,
      myseverityType:item.severityType,
      x:100,
      y:100,
      symbolSize: 14,
      myType:item.type,
      value:"75",
      itemStyle:{normal:{
        color:myColors[1],
        borderColor:'#31ac3f',
        borderWidth: 1
      }}
    }
    myDatas.push(seriesObj)
    myLinks.push(linkObj)
  })

  data.relationsPersons.risks.forEach(function(item){
    let linkObj = {
      source:item.personName,
      target:targetObj
    }
    let seriesObj = {
      name: item.personName,
      myPosition:item.position,
      mycomplaintNum:item.complaintNum,
      mypublicSentimentNum:item.publicSentimentNum,
      myseverityType:item.severityType,
      x:100,
      y:100,
      symbolSize: 14,
      myType:item.type,
      value:"75",
      itemStyle:{normal:{
        color:myColors[2],
        borderColor:'#f77d1b',
        borderWidth: 1
      }}
    }
    myDatas.push(seriesObj)
    myLinks.push(linkObj)
  })
  let myChart = echarts.init(document.getElementById(id))
  let option =  {
    color:myColors,
    legend:{
      data: data.relationsPersons.queryTypes,
      show: true,
      right: 40
    },
    tooltip:{
      show:false
    },
    series : [
      {type: 'graph',name:data.relationsPersons.queryTypes[0]},
      {type: 'graph',name:data.relationsPersons.queryTypes[1]},
      {type: 'graph',name:data.relationsPersons.queryTypes[2]},
      {
        type: 'graph',
        layout:'force',
        force:{
          edgeLength:5,
          repulsion:5
        },
        grid: {
          top:'15%',
          left: '1%',
          right: '1%',
          bottom: '15%',

        },

        symbolSize: 64,
        label: {
          normal: {
            show: false,
            textStyle:{
              fontSize:16,
              color:'#000',
            }
          },
          emphasis:{
            show:false
          }
        },
        lineStyle: {
          normal: {
            color:'#52a2a3',
            curveness: 0.2,
            width:2
          }
        },
        data: myDatas,
        links: myLinks
      }
    ]
  };
  let zArr = option.series[option.series.length-1].data
  option.series[option.series.length-1].data[zArr.length-1].x =200
  option.series[option.series.length-1].data[zArr.length-1].y =200
  myChart.setOption(option)
  myChart.on('mouseover', function (params) {
    it.toolData = params.data
    document.querySelector("#tool_tip").style.top =
      params.event.event.pageY - 120 + "px";
    document.querySelector("#tool_tip").style.left =
      params.event.event.pageX - 40 + "px";
    if(params.dataType == 'node'&& params.data.myType == '关联风险人物'){
      it.toolShow = true
    }
  });

  myChart.on('mouseout', function (params) {
    it.toolShow = false
  });
}





